.backdrop {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal__button-close {
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__button-close:hover, .modal__button-close:focus {
  fill: #2196f3;
  cursor: pointer;
}

.modal {
  width: 100%;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  transition-property: background-color, transform, width, height;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 450px) {
  .modal {
    width: 450px;
    height: 330px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    height: 400px;
  }
}

.backdrop.is-hidden .modal {
  transition: transform .5s;
  transform: scale(1.25);
}

.modal__title {
