.checkbox__form {
  justify-content: center;
  margin-bottom: 30px;
  display: flex;
}

.checkbox__label {
  align-items: center;
  display: flex;
}

.checkbox__input {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.checkbox__icon {
  width: 16px;
  height: 15px;
  fill: rgba(0, 0, 0, 0);
  background-size: contain;
  background-origin: border-box;
  border: 2px solid #000;
  border-radius: 2px;
  margin-right: 7px;
  transition-property: fill, background, border;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.checkbox__input:checked ~ .checkbox__icon {
  fill: #fff;
  background: #2196f3;
  border: 2px solid #2196f3;
  border-radius: 2px;
}

