.contacts {
    margin-bottom: 55px;

    @media screen and (min-width: 768px) {
        font-size: 14px;
        margin: 0px -10px;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 80px;
    }

    @media screen and (min-width: 1280px) {
        font-size: 16px;
        margin: 0px -15px;
        margin-bottom: 85px;
        position: relative;
    }
}

.contacts__img {
    position: absolute;
    width: 1920px;
    top: 20%;
}

.contacts__item:not(:last-child) {
    @media screen and (max-width: 767.8px) {
        margin-bottom: 20px;
    }
}

.contacts__item {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    max-width: 350px;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    padding: 43px 20px;

    background-color: #ffffff;

    @media screen and (min-width: 768px) {
        padding: 50px 31px;
        margin: 0px 10px;
        flex-basis: calc((100% - (6 * 10px)) / 3);
        // transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
        // transition-duration: 500ms;

        // &:hover,
        // &:focus {
        //     // transform: scale(1.2);
        //     // transition-duration: 500ms;
        //     // z-index: 2;
        // }
    }

    @media screen and (min-width: 1280px) {
        padding: 55px 43px;
        margin: 0px 15px;
        width: 330px;
        flex-basis: calc((100% - (6 * 15px)) / 3);
    }
}

.contacts__title {
    text-transform: uppercase;
    text-align: center;
    font-weight: 500;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.04em;
    color: #ffffff;

    display: block;
    padding: 0px 25px;
    border-radius: 4px;
    background-color: #f0d1a5;
    margin-bottom: 25px;

    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 28px;
        padding: 0px 16px;
        border-radius: 6px;
        margin-bottom: 30px;
    }

    @media screen and (min-width: 1280px) {
        margin-bottom: 32px;
    }
}

.contacts__title--bgcolor-gren {
    background-color: #c2e297;
}

.contacts__primary-text {
    font-weight: 700;
    text-align: center;
    line-height: 20px;
    letter-spacing: 0.04em;

    color: #000000;
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
        line-height: 24px;
        margin-bottom: 14px;
    }

    @media screen and (min-width: 1280px) {
        line-height: 28px;
    }
}

.contacts__secondary-text {
    font-weight: 500;
    text-align: center;
    line-height: 16px;
    color: #907e82;
    margin-bottom: 25px;

    @media screen and (min-width: 768px) {
        line-height: 24px;
        margin-bottom: 30px;
    }

    @media screen and (min-width: 1280px) {
        line-height: 28px;
        margin-bottom: 32px;
    }
}

.contacts__work-time {
    padding: 25px 0px;
    position: relative;
    margin-bottom: 25px;
    line-height: 20px;

    @media screen and (min-width: 768px) {
        line-height: 24px;
        padding: 30px 0px;
    }

    @media screen and (min-width: 1280px) {
        line-height: 28px;
        padding: 32px 0px;
    }
}

.contacts__work-time__primary-text {
    font-weight: 500;

    letter-spacing: 0.04em;
    color: #000000;
}

.contacts__work-time__primary-text--before::before {
    content: '';
    position: absolute;
    top: 0%;
    width: 100%;
    height: 1px;

    background-color: #907e82;
}

.contacts__work-time__time--margin {
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
        margin-bottom: 15px;
    }
}

.contacts__work-time__time--after::after {
    content: '';
    //   display: inline-block;
    position: absolute;
    bottom: 0%;
    left: 0%;
    width: 100%;
    height: 1px;

    background-color: #907e82;
}

.contacts__work-time__time {
    font-weight: 500;

    color: #ffa5ba;
}

.contacts__link {
    font-weight: 500;
    font-style: normal;
    line-height: 16px;
    color: #907e82;

    @media screen and (min-width: 768px) {
        line-height: 24px;
    }

    @media screen and (min-width: 1280px) {
        line-height: 28px;
    }
}

.button-contacts {
    text-align: center;

    @media screen and (max-width: 767.9px) {
        width: 250px;
        margin-left: auto;
        margin-right: auto;
    }

    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;
    }
}

.button-contacts__locations {
    color: #ffffff;
    background-color: #d41443;

    @media screen and (max-width: 767.9px) {
        margin-bottom: 10px;
    }

    @media screen and (min-width: 768px) {
        margin-right: 15px;
    }

    &:hover,
    &:focus {
        background-color: #c7133e;
    }
}

.button-contacts__franchise {

    &:hover,
    &:focus {
        background-color: #ecebeb;
    }
}

.button--modification {
    font-weight: 700;
    min-width: 162px;
    padding: 11px 0px;

    @media screen and (min-width: 768px) {
        min-width: 190px;
    }

    @media screen and (min-width: 1280px) {
        min-width: 195px;
    }
}

.button-contacts__svg {
    margin-left: 13px;

    height: 9px;
    width: 6px;
}

.button-contacts__svg--color-white {
    fill: #ffffff;
}

.button-contacts__svg--color-red {
    fill: #d41443;
}

.contacts__button {
    display: block;
    margin: 0 auto;

    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 30px;
    padding-right: 30px;

    border: none;
    cursor: pointer;

    font-family: 'Open Sans', sans-serif;
    font-size: 20px;

    background-color: #ffa500;
}